    grid-template-columns: repeat(3, min-content);
    grid-template-rows: min-content min-content;
  }
  .donate__form fieldset {
    grid-column: 1/4;
    grid-row: 1;
    display: flex;
    column-gap: 20px;
  }
  .donate__form input {
    width: 150px;
  }
  .donate__form textarea {
    width: 272px;
  }

  .donate__button {
    grid-column: 3;
    grid-row: 2;
    margin-left: auto;
  }

  .donate__confidentiality {
    text-align: left;
    grid-column: 1/3;
    grid-row: 2;
  }
}
@media (min-width: 992px) {
  .donate__form fieldset {
    column-gap: 60px;
  }
  .donate__form input {
    width: 180px;
    height: 17px;
  }
  .donate__form textarea {
    height: 96px;
  }

  .donate__contact-zone,
.donate__name-zone {
    row-gap: 40px;
  }
}
@media (min-width: 1360px) {
  .donate {
    padding-top: 100px;
  }

  .donate__title {
    margin-bottom: 80px;
  }

  .donate__form fieldset {
    column-gap: 95px;
  }
  .donate__form input {
    width: 210px;
  }
  .donate__form textarea {
    width: 320px;
    height: 107px;
  }

  .donate__contact-zone,
.donate__name-zone {
    row-gap: 50px;
  }
}
.main-footer__contact-block {
